.clear-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.container {
  max-width: 1166px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;

  @media (max-width: $tablet-width) {
    max-width: 100%;
    padding-right: 35px;
    padding-left: 35px;
  }
  @media (max-width: $mobile-width) {
    padding-right: 14px;
    padding-left: 14px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.blocked {
  position: fixed;
  overflow: hidden;
  z-index: 0;
}
